.App {
  text-align: center;
}

.bio,
.links {
  margin: 20px 0;
}

.links a {
  color: #61dafb;
  margin: 0 10px;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  transition: border-bottom 0.3s;
}

.links a:hover {
  border-bottom: 2px solid #61dafb;
}

strong {
  color: #61dafb;
}
